import React from 'react';

import * as styles from './Videos.module.css'
import { MyVideos } from '../commonComponents/Constants'

const Videos = () => {

    const onVideoImageClick = (e, src) => {
        const element = e.target;
        var iframe = document.createElement('iframe');
        Object.assign(iframe, {
            width: '560',
            height: '315',
            src,
            title: 'YouTube video player',
            frameborder: '0',
            allow: 'accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture',
            allowfullscreen: '1',
            autoplay: '1'
        })
        element.parentNode.replaceChild(iframe, element);
    }

    return (
        <div className={styles.main}>
            <section className={styles.header}>
                I'm a beginner guitarist, still trying to learn this beautiful instrument and recording a couple of my attempts at songs I Love
                <span role="img" aria-label='guitar'>‚ù§Ô∏èüé∏üé∏</span>
            </section>
            <section className={styles.videoSection}>
                {/* {MyVideos.map(video => (<div className={styles.videoPanel}>
                    <iframe width="560" height="315" src={video.src}
                        title="YouTube video player" frameborder="0"
                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                        allowfullscreen></iframe>
                </div>))} */}
                {MyVideos.map(video => (
                    <div key={video.name} className={styles.videoPanel}>
                        <div>{video.name}</div>
                        <img alt={`Guitar Pic for ${video.name}`} src={require(`../images/${video.image}`)}
                            width='560' height='315'
                            onClick={(e) => onVideoImageClick(e, video.src)}>
                        </img>
                    </div>
                ))}
            </section>
        </div>
    );
};

export default Videos;